
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapState, mapGetters } from 'vuex'
export default{
data () {
return {
note: '',
content: '',
selOrderState: '',
selManage: '',
orderState: ['', '', ''],
manage: ['', '', '', '', '']
}
},
components: {
Group, Cell, XTextarea, Selector, XButton, Box
},
created () {
this.selOrderState = this.currentOrder.state
},
computed: {
...mapState({}),
...mapGetters({
currentOrder: 'currentOrder'
})
},
mounted () {
},
destroyed () {
},
methods: {
submitData (orders) {
let manage = {
type: this.selManage,
date: dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss'),
operator: this.currentOrder.receiver,
note: this.note,
content: this.content
}
let orderState = this.selOrderState
if (this.content !== '' && this.selManage !== '') {
this.$vux.toast.show({
time: 1000,
type: 'success',
text: ''
})
this.$store.commit('updateData', {manage: manage, orderState: orderState})
this.$router.go(-1)
} else {
this.$vux.toast.show({
type: 'warn',
text: '!'
})
}
}
}
}



